export default {
  data() {
    return {
      form: {
        need_coin: "",
        award_coin: "",
        salary_days: ""
      },
      rules: {
        need_coin: [{ required: true, message: "不可以为空", trigger: "blur" }],
        award_coin: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        salary_days: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ]
      },
      keys: "",
      id: "",
      allData: {},
      loading: false
    };
  },
  created() {
    this.initData();
  },
  methods: {
    send(formName, type) {
      // console.log(this.allData);
      this.$refs[formName].validate(async valid => {
        if (valid) {
          let all = true; //quanbu shi shuzi
          Object.values(this.form).forEach(item => {
            if (!isNaN(item)) {
              all = true;
            } else {
              all = false;
            }
          });
          if (all) {
            if (type === 1) {
              let { data } = await this.$http.HallFunConfig.PutActivityNew5({
                keys: this.keys,
                values: JSON.stringify(this.allData),
                id: this.id
              });
              // console.log(data);
              if (data.code === 1 && data.msg === "ok") {
                this.$message({
                  type: "success",
                  message: "保存成功!"
                });
              } else {
                this.$message({
                  type: "warning",
                  message: "保存失败!"
                });
              }
            } else if (type === 2) {
              this.loading = true;
              let { data } = await this.$http.HallFunConfig.PostActivityNew5({
                keys: this.keys,
                values: JSON.stringify(this.allData),
                id: this.id
              });
              // console.log(data);
              if (data.code === 1 && data.msg === "ok") {
                this.loading = false;
                this.$message({
                  type: "success",
                  message: "发送服务器配置成功!"
                });
              } else {
                this.loading = false;
                this.$message({
                  type: "warning",
                  message: "发送服务器配置失败!"
                });
              }
            }
          } else {
            this.$message({
              type: "warning",
              message: "所有项都必须是数字!"
            });
            return false;
          }
        } else {
          this.$message({
            type: "warning",
            message: "必填的项不可以为空!"
          });
          return false;
        }
      });
    },
    async initData() {
      let { data } = await this.$http.HallFunConfig.GetActivityNew5({
        key: "activity_new.lua"
      });
      //   console.log(data);
      this.keys = data.data[0].sys_key;
      this.id = data.data[0].id;
      let res = data.data[0].sys_val;
      this.allData = JSON.parse(res);
      //   console.log(this.keys, this.id, this.allData);
      Object.keys(this.allData).forEach(item => {
        if (this.allData[item].ac_type === "10005") {
          this.form = this.allData[item].ac_content;
        }
      });
    }
  }
};



